  letter-spacing: .04em;
  text-transform: uppercase;
  margin-top: 165px;
  font-family: Titan One, sans-serif;
  font-size: 30px;
  font-weight: 400;
  line-height: 34px;
}

.products__ice-cream {
  width: 368px;
  height: 535px;
  background-color: #ffb8ca;
  border-radius: 24px;
  margin-top: 281px;
  position: relative;
}

.products-image-cream:before {
  width: 368px;
  height: 394px;
  content: "";
  background-image: url("home-pic1.551de200.png");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  bottom: 75%;
}

.products__ice-coffee {
  width: 368px;
  height: 535px;
  background-color: #f0d1a5;
  border-radius: 24px;
  margin-top: 281px;
  position: relative;
}

.products-image-coffee:before {
  width: 368px;
  height: 394px;
  content: "";
  background-image: url("home-pic2.62199daf.png");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  bottom: 75%;
}

.products-milkshakes {
  width: 368px;
  height: 535px;
  background-color: #c2e297;
  border-radius: 24px;
  margin-top: 281px;
  position: relative;
}

.products-image-milk:before {
  width: 368px;
  height: 394px;
  content: "";
  background-image: url("home-pic3.775fe778.png");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  bottom: 75%;
}

.products-modal {
  color: #fff;
  text-align: center;
  letter-spacing: .04em;
  width: 368px;
  height: 250px;
  padding-left: 32px;
  padding-right: 32px;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 28px;
  display: flex;
}

.products__modal-cream {
  background-color: #ffb8ca;
  border-radius: 30px;
  display: none;
  position: absolute;
  top: 150px;
}

.products__modal-coffee {
  background-color: #f0d1a5;
  border-radius: 30px;
  display: none;
  position: absolute;
  top: 150px;
}

.products__modal-milk {
  background-color: #c2e297;
  border-radius: 30px;
  display: none;
  position: absolute;
  top: 150px;
}

.products-description {
  color: #fff;
  text-align: center;
  letter-spacing: .04em;
  padding-left: 32px;
  padding-right: 32px;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 28px;
}

.products__button-svg {
  justify-content: center;
  margin-top: 32px;
  display: flex;
}

.products__svg-button {
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.products__svg-button:hover, .products__svg-button:focus {
  transition: all .2s ease-in-out;
  transform: rotate(-.25turn);
}

.span-dot {
  justify-content: center;
  gap: 4px;
  padding-top: 32px;
  padding-bottom: 32px;
  display: flex;
}

[role="button"] {
  cursor: pointer;
  width: 6px;
  height: 6px;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  transition: all .5s ease-out;
  display: inline-block;
}

[role="button"]:hover, [role="button"]:focus {
  background-color: #d4d4d4;
  transition: all .5s ease-out;
}

