@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Titan+One&display=swap');

$tablet-breackpoint: 321px;
$desktop-breackpoint: 769px;

li {
  list-style: none;
}

.global-header {
  background: #ffb8ca;
  height: 100px;
}

.container--header {
  margin: 0 auto;
  width: 1200px;
}

.global-main {
  background: #e5e5e5;
  height: 700px;
}

.global-footer {
  background: #ffb8ca;
  height: 100px;
}

.backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.2);
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s ease;
}

.modal {
  position: relative;
  padding: 40px;
  background: #e17992;
  width: 600px;
  height: 700px;
  margin: 0 auto;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.modal-2 {
  position: relative;
  padding: 40px;
  background: #ffb8ca;
  width: 600px;
  height: 470px;
  margin: 0 auto;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.modal-3 {
  position: relative;
  padding: 40px;
  background: #ffb8ca;
  width: 600px;
  height: 470px;
  margin: 0 auto;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
}

.is-hidden {
  transform: scale(1.1);
  visibility: hidden;
}

.modal__close-btn {
  position: absolute;
  background: #e17992;
  right: 15px;
  top: 15px;
  width: 35px;
  height: 35px;
  border: 1px solid #e18298;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.modal__close-btn .icon {
  fill: #ffffff;
  width: 25px;
  height: 25px;
  padding-right: 2px;
  padding-top: 1px;
}
.modal__close-btn:hover {
  background: #ffffff;
}

.modal__close-btn .icon:hover {
  fill: #d41443;
}

.modal-2-3__close-btn {
  position: absolute;
  background: #ffffff;
  right: 15px;
  top: 15px;
  width: 35px;
  height: 35px;
  border: 1px solid #e17992;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.modal-2-3__close-btn .icon {
  fill: #d41443;
  width: 25px;
  height: 25px;
  padding-right: 2px;
  padding-top: 1px;
}
.modal-2-3__close-btn:hover {
  background: #d41443;
}

.modal-2-3__close-btn .icon:hover {
  fill: #ffffff;
}

.modal__products-list {
  display: flex;
  padding-left: 0;
  justify-content: center;
  align-items: center;
}

.modal__products-list__items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.modal__forms--form-group {
  position: relative;
  display: flex;
  gap: 4px;
  flex-direction: column;
}

.modal__forms__form-group-3 {
  position: relative;
  display: flex;
  gap: 4px;
  flex-direction: column;
}

.modal__heading {
  font-family: 'Titan One', cursive;
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 34px;
  text-align: center;
  letter-spacing: 0.04em;

  color: #ffffff;

  text-transform: uppercase;
  margin-top: -15px;
  margin-bottom: 15px;
}

.modal__form-heading-2-3 {
  font-family: 'DM Sans', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 36px;
  /* identical to box height, or 188% */
  text-align: center;

  color: #d41443;

  margin-bottom: 15px;
}

.modal__heading--sub-heading {
  font-family: 'Titan One', cursive;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.04em;
  color: #ffffff;
  text-transform: uppercase;
}

.modal__forms--form-group + .modal__forms--form-group {
  margin-top: 10px;
}

.modal__forms--form-group__selctor {
  font-family: 'DM Sans', sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.04em;
  color: #ffffff;
  margin-left: 30px;
}

.modal__forms--form-group__input {
  padding-left: 40px;
  padding-top: 4px;
  padding-bottom: 4px;
  border: 2px solid #e18298;
  background: #ffb8ca;
  border-radius: 8px;
  font-family: 'DM Sans', sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
  letter-spacing: 0.04em;
  color: #d41443;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  margin-left: 30px;
  margin-right: 30px;
}

.modal__forms--form-group__input::placeholder {
  color: #d41443;
}

.modal__forms__form-group-3 + .modal__forms__form-group-3 {
