.modal__products {
  margin-top: -25px;
}

.modal__products-list__items--qty--input {
  width: 100px;
  margin-top: 10px;
  padding-left: 60px;
  padding-top: 2px;
  padding-bottom: 2px;
  border: 2px solid #e18298;
  background: #ffb8ca;
  border-radius: 8px;
  font-family: 'DM Sans', sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
  letter-spacing: 0.04em;
