  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}

.modal__products-list__items--qty--input::placeholder {
  color: #d41443;
}

.modal__products-list__items--qty--input:focus-within {
  border: 2px solid #d41443;
  border-radius: 4px;
  cursor: pointer;
}

.modal__forms--form-group__input:focus-within {
  border: 2px solid #d41443;
  border-radius: 4px;
  cursor: pointer;

  outline: none;
}

.modal__forms__form-group-3--input:focus-within {
  border: 3px solid #d41443;
  border-radius: 8px;
  cursor: pointer;

  outline: none;
}

.input:focus ~ .icon--adress {
  fill: #d41443;
}

.modal__forms .icon--adress {
  position: absolute;
  width: 18px;
  height: 18px;
  fill: currentColor;
  top: 60%;
  left: 42px;
}

.modal__form-controls {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.modal__form-controls--btn-submit-1 {
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 22px;
  padding: 10px 40px;

  font-family: 'DM Sans', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  /* identical to box height, or 188% */
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.06em;
  border: none;
  outline: none;
  cursor: pointer;
  max-width: 200px;
  color: #d41443;
}

.modal__form-controls--btn-submit-1:hover,
.modal__form-controls--btn-submit-1:focus {
  background: #ffb8ca;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 22px;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.modal__form-controls--btn-submit {
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 22px;
  padding: 10px 40px;

  font-family: 'DM Sans', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  /* identical to box height, or 188% */
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.06em;
  border: none;
  outline: none;
  cursor: pointer;
  max-width: 400px;
  color: #d41443;
}

.modal__form-controls--btn-submit:hover,
.modal__form-controls--btn-submit:focus {
  background: #d41443;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 22px;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  color: #ffffff;
}

.modal__forms__google-map {
  padding-bottom: 50%;
  position: relative;
  display: flex;
  justify-content: center;
}

.modal__forms__google-map--position {
  height: 100%;
  width: 100%;
  position: absolute;
  border: 3px solid #d41443;
}

.modal__forms--location {
  font-family: 'DM Sans', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 34px;
  text-align: center;
  color: #d41443;
  margin-top: 10px;
}

.modal__form-textarea {
  position: relative;
  display: flex;
  gap: 4px;
  flex-direction: column;
  margin-top: 10px;
}

.modal__form-textarea--selector {
  font-family: 'DM Sans', sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.04em;
  color: #d41443;
  margin-left: 30px;
}

.modal__form-textarea--input {
  padding: 12px 16px;
  border: 2px solid #d41443;
  border-radius: 8px;
  font-family: 'DM Sans', sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
  letter-spacing: 0.04em;
  color: #d41443;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  margin-left: 30px;
  margin-right: 30px;
  resize: none;
}

.modal__form-textarea--input::placeholder {
  color: #d41443;
}

.modal__form-textarea--input:focus-within {
  border: 3px solid #d41443;
  border-radius: 8px;
  cursor: pointer;
  outline: none;
}

.modal__products-tablet {
  display: none;
}

